import React, { useState, useEffect, useContext } from 'react';
import PropTypes from 'prop-types';
import Context from '../context';
import './EditTodoModal.css'; // –ò—Å–ø–æ–ª—å–∑—É–µ–º —Å–æ–∑–¥–∞–Ω–Ω—ã–µ —Å—Ç–∏–ª–∏

function EditTodoModal({ isOpen, todo }) {
  const { editTodo, closeEditModal } = useContext(Context);

  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [hours, setHours] = useState(0);
  const [minutes, setMinutes] = useState(0);
  const [seconds, setSeconds] = useState(0);

  useEffect(() => {
    if (todo) {
      setTitle(todo.title);
      setDescription(todo.description || ''); // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –æ–ø–∏—Å–∞–Ω–∏–µ –∏–ª–∏ –ø—É—Å—Ç—É—é —Å—Ç—Ä–æ–∫—É

      const totalSeconds = todo.duration || 0;
      setHours(Math.floor(totalSeconds / 3600));
      setMinutes(Math.floor((totalSeconds % 3600) / 60));
      setSeconds(totalSeconds % 60);
    } else {
      setTitle('');
      setDescription('');
      setHours(0);
      setMinutes(0);
      setSeconds(0);
    }
  }, [todo]);

  const handleSubmit = (event) => {
    event.preventDefault();
    if (!todo) return;

    const newTitle = title.trim();
    const newDescription = description.trim();
    const totalSeconds = (parseInt(hours, 10) || 0) * 3600 +
                         (parseInt(minutes, 10) || 0) * 60 +
                         (parseInt(seconds, 10) || 0);

    if (newTitle) {
      // –ü–µ—Ä–µ–¥–∞–µ–º —Ç–∞–∫–∂–µ newDescription –≤ editTodo
      editTodo(todo.id, newTitle, totalSeconds, newDescription);
      closeEditModal(); // –ó–∞–∫—Ä—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –ø–æ—Å–ª–µ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è
    }
  };

  const handleClose = () => {
    closeEditModal();
  };

  if (!isOpen || !todo) {
    return null; // –ù–µ —Ä–µ–Ω–¥–µ—Ä–∏–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ, –µ—Å–ª–∏ –æ–Ω–æ –Ω–µ –æ—Ç–∫—Ä—ã—Ç–æ –∏–ª–∏ –Ω–µ—Ç –∑–∞–¥–∞—á–∏
  }

  return (
    <div className="modal">
      <div className="modal-body edit-todo-modal-body"> {/* –ò—Å–ø–æ–ª—å–∑—É–µ–º —Å–ø–µ—Ü–∏—Ñ–∏—á–Ω—ã–π –∫–ª–∞—Å—Å –¥–ª—è —Ç–µ–ª–∞ –º–æ–¥–∞–ª–∫–∏ */}
        <h2>–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –∑–∞–¥–∞—á—É</h2>
        <form onSubmit={handleSubmit}>
          <div>
            <label htmlFor="todoTitle">–ù–∞–∑–≤–∞–Ω–∏–µ:</label>
            <input
              type="text"
              id="todoTitle"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              required
            />
          </div>
          <div>
            <label htmlFor="todoDescription">–û–ø–∏—Å–∞–Ω–∏–µ:</label>
            <textarea
              id="todoDescription"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              rows="3"
            />
          </div>
          <div className="time-inputs">
            <div>
              <label htmlFor="todoHours">–ß–∞—Å—ã:</label>
              <input
                type="number"
                id="todoHours"
                value={hours}
                onChange={(e) => setHours(e.target.value)}
                min="0"
              />
            </div>
            <div>
              <label htmlFor="todoMinutes">–ú–∏–Ω—É—Ç—ã:</label>
              <input
                type="number"
                id="todoMinutes"
                value={minutes}
                onChange={(e) => setMinutes(e.target.value)}
                min="0"
                max="59"
              />
            </div>
            <div>
              <label htmlFor="todoSeconds">–°–µ–∫—É–Ω–¥—ã:</label>
              <input
                type="number"
                id="todoSeconds"
                value={seconds}
                onChange={(e) => setSeconds(e.target.value)}
                min="0"
                max="59"
              />
            </div>
          </div>
          <div className="modal-buttons">
            <button type="submit">–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</button>
            <button type="button" onClick={handleClose}>–û—Ç–º–µ–Ω–∞</button>
          </div>
        </form>
        <button className="close-button" onClick={handleClose}>&times;</button>
      </div>
    </div>
  );
}

EditTodoModal.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  todo: PropTypes.object, // –ú–æ–∂–µ—Ç –±—ã—Ç—å null, –µ—Å–ª–∏ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –∑–∞–∫—Ä—ã—Ç–æ
};

export default EditTodoModal;
